'use client';
import { FC } from 'react';
import { Typography, ButtonBase } from '@mui/material';
import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { StyledBottomNavigation, classes } from './styles';
import {
  bottomNavigationTabs as tabs,
  bottomNavigationCheckRouteActive as checkRouteActive,
} from './data';

const BottomNavigation: FC = () => {
  const pathname = usePathname() ?? '';

  return (
    <StyledBottomNavigation>
      {tabs.map(item => {
        const isActive = checkRouteActive(pathname, item);

        return (
          <ButtonBase
            component={Link}
            href={item.route}
            aria-current={isActive ? 'page' : undefined}
            className="flex flex-col h-16 gap-2 min-w-24"
            key={item.route}
          >
            <div className={isActive ? classes.iconActive : classes.iconInactive}>
              {isActive ? item.iconFilled : item.iconOutlined}
            </div>
            <Typography
              variant="caption"
              className={isActive ? classes.textActive : classes.textInactive}
            >
              {item.key.charAt(0).toUpperCase() + item.key.slice(1)}
            </Typography>
          </ButtonBase>
        );
      })}
    </StyledBottomNavigation>
  );
};

export default BottomNavigation;
